.container {
    height: 100%;
    display: flex;
    justify-content: center;
    background-color: #61677A;
    color: white;
    font-family: 'Times New Roman', Times, serif;
}
.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;    
    font-weight: 1000;
    font-size: 400%;
}
.movie {
    width: 80%;
}
.movie_bg_img {
    width: 100%;
}
.movie_header {
    min-height: 200px;
}
.movie_big_title {
    align-items: flex-start;
    display: flex;
    margin-top: 112px;
}
.movie_img {
    border-width: 0;
    box-shadow:  0 1px 2px 0 rgb(60 64 67 / 30%), 0 1px 3px 1px rgb(60 64 67 / 15%);
    border-radius: 4px;
    margin: 7px 24px 0 0;
    width: 72px;
}
.movie_title {
    max-height: 300px;
    line-height: 2rem;
    font-size: 1.5rem;
    letter-spacing: 0;
    font-weight: 400;
    align-items: flex-end;
    display: flex;
    width: 100%;
    word-break: keep-all;
}
.movie__year {
    margin-top: 2px;
    fill: #5f6368;
    stop-color: #5f6368;
    line-height: 1rem;
    font-size: 0.75rem;
    letter-spacing: 0.025em;
    font-weight: 400;
  }
  .movie__rate__row {
    align-items: flex-start;
    display: flex;
    margin: 10px;
  }
  .movie__rate {
    padding: 0 16px;
    min-width: 96px;
  }
  .movie__content {
    fill: #5f6368;
    stop-color: #5f6368;
    font-size: 0.875rem;
    font-weight: 400;
    letter-spacing: 0.0142857143em;
    line-height: 1.25rem;
    font-family: Roboto, Helvetica, Arial, sans-serif;
  }
  .movie__genres {
    margin-top: 30px;
  }
  
  .movie__genres span {
    padding-right: 20px;
  }