.movie {
    background-color: white;
    margin-bottom: 70px;
    font-weight: 500;
    padding: 20px;
    border-radius: 10px;
    color: #555555;
    color: inherit; /*부모 객체 색상 그대로 사용*/
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px rgba(0, 0, 0, 0.025);
}

.movie_img {
    position: relative; /*element가 원래 본인 기준으로 위치 잡음*/
    top : -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow:  0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px rgba(0, 0, 0, 0.025);
}

.movie_title {
    margin: 0;
    font-weight: 1000;
    font-size: 150%;
    text-decoration: none;
}

.movie_genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
}

.movie_genres li {
    margin-right: 10px;
    font-size: 14px;
}